import React, {Component} from 'react'
import BaseContent from '@/components/BaseContent'
import ReactMarkdown from 'react-markdown'

import {connect} from 'dva'
import {getUrlParams} from '@/utils/utils'

import styles from './style.module.less'

class QuestionItem extends Component {
  constructor (props) {
    super(props)
    this.state = {}
  }

  remove = () => {
    this.props.onRemoveCallback()
  }

  render () {
    const {title, questions_stem, index, } = this.props;

    return (
      <div className={styles.questionitem}>
        <h4>{index}：{title}</h4>
        <ReactMarkdown className="react-markdown" source={questions_stem}></ReactMarkdown>
      </div>
    )
  }
}

let mapState = store=>{
  return {...store.addExam, ...store.main}
}

@connect(mapState)
class ExamDetail extends Component{
  state = {}
  
  componentDidMount () {
      const id = getUrlParams(window.location.href).id
      this.props.dispatch({type: 'addExam/getExamDetail', exam_id: id})
  }
  render () {
    const { examDetail } = this.props
    console.log(examDetail)
    return (
      <React.Fragment>
        <div className={styles.detailContent}>
        <BaseContent style={{marginRight: 20}}>
          <div className={styles.exam}>
            {/* <p>考试时间：1小时30分钟		监考人：刘于       开始考试时间：2018.9.10  10:00  阅卷人：刘于</p> */}
            <div>
              {
                examDetail.questions.map((item, index) => <QuestionItem {...item} index={index+1} key={index} />)
              }
            </div>
          </div>
        </BaseContent>

        <BaseContent style={{width: 400, flexShrink: 0}}>
              
        </BaseContent>
        </div>
      </React.Fragment>
    )
  }
}
export default ExamDetail;